body {
  font-family: sans-serif;
}

.search-form {
  background: linear-gradient(45deg, #373b44 65%, #4286f4);
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 15px 0;
  font-size: 18px;
  display: flex;
}

.search-input {
  min-width: 300px;
  border: none;
  border-radius: 4px;
  outline: none;
  padding: 10px 20px;
}

.search-btn {
  cursor: pointer;
  color: #fff;
  opacity: 1;
  background-color: #b00;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
}

.search-btn:-webkit-any(:hover, :focus) {
  background-color: #ce0202;
}

.search-btn:is(:hover, :focus) {
  background-color: #ce0202;
}

.search-btn:active {
  opacity: .7;
}

.gallery {
  width: 1220px;
  flex-wrap: wrap;
  gap: 30px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
}

.image {
  width: 200px;
  height: 150px;
  object-fit: cover;
  margin-right: auto;
}

.photo-card {
  max-width: 200px;
  background-color: #f0f8ff;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  display: flex;
  overflow: hidden;
}

.info {
  width: 100%;
  border: 1px solid #000;
  border-top: none;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  flex-direction: column;
  padding: 10px;
  font-size: 14px;
  display: flex;
}

.info-item {
  margin: 4px;
}

.number {
  font-weight: 400;
}

.load-more {
  cursor: pointer;
  min-width: 150px;
  color: #fff;
  opacity: 1;
  background-color: #4286f4;
  border: none;
  border-radius: 5px;
  margin: 30px auto;
  padding: 10px 20px;
  display: none;
}

.load-more:-webkit-any(:hover, :focus) {
  background-color: #3468bd;
}

.load-more:is(:hover, :focus) {
  background-color: #3468bd;
}

.load-more:active {
  opacity: .7;
}

.load-more.js-visible {
  display: block;
}

